.security-dashboard-page .security .row3{
    background-image: url('../../../../assets/SecurityDashboard/row-bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding:7.5%;
    margin:5% 0;
    color:#fff;
    h1{
        font-size: 32px;
        margin-bottom: 55px;
    }
    .mini-item{
        display: flex;
        align-items: center;
        padding:20px 45px;
        background: linear-gradient(rgba(255,255,255,0.15), rgba(255,255,255,0.15));
        width: 40%;
        border-radius: 10px;
        margin:30px 0;
        &:hover{
            background: linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5));
        }
    }
    svg{
        font-size: 40px;
        margin-right: 25px;
    }
    p{
        font-size: 18px;
        flex: 1;
    }
}

@media(max-width:1401px){
    .security-dashboard-page .security .row3{
        h1{
            font-size: 27px;
            margin-bottom: 45px;
        }
        .mini-item{
            padding:20px 35px;
            width: 43%;
            border-radius: 7.5px;
            margin:25px 0;
        }
        svg{
            font-size: 35px;
            margin-right: 20px;
        }
        p{
            font-size: 15px;
        }
    }
}

@media(max-width:991px){
    .security-dashboard-page .security .row3{
        .mini-item{
            width: 55%;
        }
    }
}

@media(max-width:767px){
    .security-dashboard-page .security .row3{
        padding:35px 7.5%;
        margin:35px 0;
        h1{
            font-size: 20px;
            text-align: center;
            margin-bottom: 25px;
        }
        .mini-item{
            padding:15px 25px;
            width: 100%;
            margin:20px 0;
        }
        svg{
            font-size: 30px;
            margin-right: 15px;
        }
    }
}

//============================================
.security-dashboard-page .shared-get-started{
    margin:0;
}
.security-dashboard-page .security .row4{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin:5% 0 0;
    padding:0 7.5% 0 0;
    .img{
        content: url('../../../../assets/SecurityDashboard/img2.png');
        width: 52.5%;
    }
    .col2{
        width: 42.5%;
    }
    .item4{
        position: relative;
        width: 75%;
        border:1px solid #eee;
        border-radius: 10px;
        padding:40px 50px;
        margin:0 0 80px 0;
        &:last-child{
            margin:0 0 15px auto;
        }
    }
    .profile{
        position: absolute;
        width: 80px;
        top:-35px;
        left: -35px;
    }
    .stars{
        display: flex;
        color:#888;
        font-size: 15px;
    }
    p{
        font-size: 18px;
        margin:15px 0;
    }
    h6{
        font-size: 17px;
    }
}

@media(max-width:1401px){
    .security-dashboard-page .security .row4{
        .item4{
            padding:30px 40px;
            margin:0 0 60px 0;
            &:last-child{
                margin:0 0 10px auto;
            }
        }
        .profile{
            width: 60px;
            top:-30px;
            left: -30px;
        }
        .stars{
            font-size: 14px;
        }
        p{
            font-size: 15px;
            margin:10px 0;
        }
        h6{
            font-size: 14px;
        }
    }
}


@media(max-width:767px){
    .security-dashboard-page .security .row4{
       flex-wrap: wrap;
       flex-direction: column-reverse;
        margin:55px 0 0;
        padding:0 7.5%;
        .img{
            width: 350px;
            max-width: 100%;
            margin:20px auto 0;
        }
        .col2{
            width: 100%;
        }
        .item4{
            width: 87.5%;
            padding:22.5px;
            margin:0 0 50px 0;
        }
        .profile{
            position: absolute;
            width: 50px;
            top:-20px;
            left: -20px;
        }
        .stars{
            font-size: 13px;
        }
    }
}

@-webkit-keyframes hvr-icon-pop {
    50% {
      -webkit-transform: scale(1.25);
      transform: scale(1.25);
    }
}
@keyframes hvr-icon-pop {
    50% {
      -webkit-transform: scale(1.25);
      transform: scale(1.25);
    }
}

.security-dashboard-page .security .row4{
    .item4{
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        &:hover .stars{
            color:rgb(241, 239, 117);
        }
    }
    .item4 .profile{
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-property: transform;
        transition-property: transform;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
    }
    .item4:hover .profile, .item4:focus .profile,.item4:active .profile{
        -webkit-animation-name: hvr-icon-pop;
        animation-name: hvr-icon-pop;
        -webkit-animation-duration: 0.3s;
        animation-duration: 0.3s;
        -webkit-animation-timing-function: linear;
        animation-timing-function: linear;
        -webkit-animation-iteration-count: 2;
        animation-iteration-count: 2;
    }

}


@-webkit-keyframes hvr-icon-wobble-vertical {
    16.65% {
      -webkit-transform: translateY(6px);
      transform: translateY(6px);
    }
    33.3% {
      -webkit-transform: translateY(-5px);
      transform: translateY(-5px);
    }
    49.95% {
      -webkit-transform: translateY(4px);
      transform: translateY(4px);
    }
    66.6% {
      -webkit-transform: translateY(-2px);
      transform: translateY(-2px);
    }
    83.25% {
      -webkit-transform: translateY(1px);
      transform: translateY(1px);
    }
    100% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
    }
}
@keyframes hvr-icon-wobble-vertical {
    16.65% {
      -webkit-transform: translateY(6px);
      transform: translateY(6px);
    }
    33.3% {
      -webkit-transform: translateY(-5px);
      transform: translateY(-5px);
    }
    49.95% {
      -webkit-transform: translateY(4px);
      transform: translateY(4px);
    }
    66.6% {
      -webkit-transform: translateY(-2px);
      transform: translateY(-2px);
    }
    83.25% {
      -webkit-transform: translateY(1px);
      transform: translateY(1px);
    }
    100% {
      -webkit-transform: translateY(0);
      transform: translateY(0);
    }
}

.security-dashboard-page .security .row3{
    .mini-item{
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
    }
    .mini-item svg{
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
    }
    .mini-item:hover svg{
        -webkit-animation-name: hvr-icon-wobble-vertical;
        animation-name: hvr-icon-wobble-vertical;
        -webkit-animation-duration: 1s;
        animation-duration: 1s;
        -webkit-animation-timing-function: ease-in-out;
        animation-timing-function: ease-in-out;
        -webkit-animation-iteration-count: 1;
        animation-iteration-count: 1;
    }
}