.security-dashboard-page .features .row1{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin:5% 0;
    padding:0 10%;
    .col1{
        width: 45%;
        position: relative;
        transition: 0.25s ease-in-out;
        &:hover{
            .inner{
                transform: scale(1.035);
                transition: 0.25s ease-in-out;
                h6{
                    color:#64f87d;
                    transition: 0.25s ease-in-out;
                }
            }
            .bg-img{
                border-radius: 15%;
                transition: 0.25s ease-in-out;
            }
        }
    }
    .col2{
        width: 40%;
    }
    .bg-img{
        content: url('../../../../assets/SecurityDashboard/img1.png');
        border-radius: 30px;
        width: 85%;
    }
    .percentage{
        width: 30%;
        position: absolute;
        right: 0;
        top:15%;
    }
    .img{
        content: url('../../../../assets/SecurityDashboard/percentage.png');
        width: 100%;
    }
    .inner{
        position: relative;
    }
    .info{
        position: absolute;
        top:50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 77.5%;
        height: 77.5%;
        border-radius: 50%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        h6{
            font-size: 40px;
            margin-bottom: 10px;
        }
        span{
            font-size: 18px;
        }
    }
    h1{
        font-size: 35px;
    }
    .p{
        font-size: 19px;
        margin:20px 0 35px;
    }
    .mini-item{
        display: flex;
        align-items: center;
        margin:15px 0;
        svg{
            color:#427cff;
            font-size: 40px;
            margin-right: 25px;
        }
        p{
            font-size: 17px;
            flex: 1;
        }
        &:hover{
            p{
                color:#427cff;
            }
        }
    }
}

@media(max-width:1401px){
    .security-dashboard-page .features .row1{
        .bg-img{
            border-radius: 25px;
        }
        .percentage{
            width: 30%;
            top:15%;
        }
        
        .info{
            h6{
                font-size: 32px;
                margin-bottom: 7.5px;
            }
            span{
                font-size: 15px;
            }
        }
        .col2{
            width: 42.5%;
        }
        h1{
            font-size: 27.5px;
        }
        .p{
            font-size: 16px;
            margin:15px 0 30px;
        }
        .mini-item{
            margin:15px 0;
            svg{
                font-size: 32px;
                margin-right: 20px;
            }
            p{
                font-size: 14px;
            }
        }
    }
}

@media(max-width:950px){
    .security-dashboard-page .features .row1{
        margin:7.5% 0;
        padding:0 7.5%;
        .col1{
            width: 47.5%;
        }
        .col2{
            width: 45%;
        }
        .percentage{
            width: 35%;
            top:10%;
        }
        .info{
            h6{
                font-size: 28px;
                margin-bottom: 5px;
            }
        }
    }
}

@media(max-width:767px){
    .security-dashboard-page .features .row1{
        margin:10px 0 25px;
        flex-wrap: wrap;
        .col1{
            width: 400px;
            max-width: 100%;
            margin:20px auto;
        }
        .col2{
            width: 100%;
            margin:20px 0;
        }
        .bg-img{
            border-radius: 20px;
        }
        .info{
            h6{
                font-size: 25px;
            }
            span{
                font-size: 14px;
            }
        }
        h1{
            font-size: 20px;
        }
        .p{
            font-size: 15px;
            margin:10px 0 20px;
        }
        .mini-item{
            margin:10px 0;
            svg{
                font-size: 25px;
                margin-right: 15px;
            }
        }
    }
}

.security-dashboard-page .features .row2{
    margin:7.5% 0 5%;
    padding:0 10%;
    h1{
        font-size: 35px;
        text-align: center;
        margin-bottom: 15px;
    }
    .items2{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .item2{
        border:1px solid transparent;
        width: 30%;
        padding:25px;
        text-align: center;
        border-radius: 10px;
        margin:15px 0 5px;
        &:hover{
            transition: 0.3s ease-in-out;
            .icon{
                border-radius: 69% 31% 37% 63% / 49% 61% 39% 51%;
                transition: 0.3s ease-in-out;
            }
            .icon svg{
                transform: scale(1.2);
                color:#f6f9ff;
                transition: 0.3s ease-in-out;
            }
        }
    }
    .icon{
        background-color: #427cff;
        width: 80px;
        height: 80px;
        border-radius: 50%;
        color:#fff;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 45px;
        margin:0 auto;
    }
    h6{
        margin:20px 0 15px;
        font-size: 18px;
    }
    p{
        font-size: 17.5px;
        margin-bottom: 20px;
    }
    .item2 a{
        display: flex;
        align-items: center;
        justify-content: center;
        color:#427cff;
        font-size: 18px;
        svg{
            margin-left: 5px;
        }
    }
    .item2:hover a{
        opacity: 1;
    }
    .link{
        text-align: center;
        margin-top: 25px;
    }
    .link a{
        padding:15px 35px;
        border:1px solid #427cff;
        color:#427cff;
        font-size: 18px;
        margin:0 auto;
        border-radius: 25px;
    }
}

@media(max-width:1401px){
    .security-dashboard-page .features .row2{
        h1{
            font-size: 30px;
            margin-bottom: 10px;
        }
        .item2{
            padding:20px;
            margin:10px 0 5px;
        }
        .icon{
            width: 60px;
            height: 60px;
            font-size: 35px;
        }
        h6{
            margin:17px 0 12px;
            font-size: 15px;
        }
        p{
            font-size: 15px;
            margin-bottom: 15px;
        }
        .item2 a{
            font-size: 15px;
        }
        .link{
            margin-top: 20px;
        }
        .link a{
            padding:12px 30px;
            font-size: 15px;
            border-radius: 25px;
        }
    }
}

@media(max-width:900px){
    .security-dashboard-page .features .row2{
        .item2{
            width: 47.5%;
        }
    }
}

@media(max-width:525px){
    .security-dashboard-page .features .row2{
        margin:45px 0;
        padding:0 7.5%;
        h1{
            font-size: 20px;
            margin-bottom: 5px;
        }
        .item2{
            width: 100%;
            margin:5px 0;
            padding:15px;
        }
        .icon{
            width: 55px;
            height: 55px;
            font-size: 30px;

        }
        .link{
            margin-top: 10px;
        }
        .link a{
            padding:10px 20px;
            border-radius: 20px;
        }
    }
}

@-webkit-keyframes hvr-icon-buzz {
    50% {
      -webkit-transform: translateX(3px) rotate(2deg);
      transform: translateX(3px) rotate(2deg);
    }
    100% {
      -webkit-transform: translateX(-3px) rotate(-2deg);
      transform: translateX(-3px) rotate(-2deg);
    }
}
@keyframes hvr-icon-buzz {
    50% {
      -webkit-transform: translateX(3px) rotate(2deg);
      transform: translateX(3px) rotate(2deg);
    }
    100% {
      -webkit-transform: translateX(-3px) rotate(-2deg);
      transform: translateX(-3px) rotate(-2deg);
    }
}

.security-dashboard-page .features .row2{
    .item2{
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
    }
    .item2 a svg{
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
    }
    .item2:hover a svg, .item2:focus a svg, .item2:active a svg{
        -webkit-animation-name: hvr-icon-buzz;
        animation-name: hvr-icon-buzz;
        -webkit-animation-duration: 0.5s;
        animation-duration: 0.5s;
        -webkit-animation-timing-function: linear;
        animation-timing-function: linear;
        -webkit-animation-iteration-count: infinite;
        animation-iteration-count: infinite;
    }

    .link a{
        display: inline-block;
        vertical-align: middle;
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        position: relative;
        background: transparent;
        -webkit-transition-property: color;
        transition-property: color;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
    }
    .link a:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: -1px;
        left: -1px;
        right: -1px;
        bottom: -1px;
        background: #427cff;
        border-radius: 25px;
        -webkit-transform: scale(0);
        transform: scale(0);
        -webkit-transition-property: transform;
        transition-property: transform;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
    }
    .link a:hover, .link a:focus, .link a:active {
        color: white;
        border:1px solid transparent;
    }
    .link a:hover:before, .link a:focus:before,.link a:active:before {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}  