.security-dashboard-page{
    overflow: hidden;
    .shared-banner{
        background-image: url('../../../../assets/SecurityDashboard/banner.png') !important;
        background-attachment: unset;
        padding:12.5% 10% 15%;
        position: relative;
    }
    .shared-banner .banner-img{
        position: absolute;
        content: url('../../../../assets/SecurityDashboard/banner-img.png');
        right:-5%;
        top:25%;
        width: 45%;
    }
}

@media(max-width:767px){
    .security-dashboard-page{
        .shared-banner{
            padding:120px 7.5% 25px;
            display: block;
        }
        .shared-banner .banner-img{
            position: relative;
            right:unset;
            top:unset;
            width: 95%;
            margin:45px auto 0;
        }
    }
}